<!-- 人员管理 -->
<template>
  <div>
    <Row :gutter="20">
      <Col :lg="4" :md="8">
        <Input v-model="queryForm.userName" placeholder="工号"></Input>
      </Col>
      <Col :lg="4" :md="8">
        <Input v-model="queryForm.userJobNo" placeholder="姓名"></Input>
      </Col>
      <Col :lg="4" :md="8">
        <Button type="success" @click="page()">
          <Icon type="md-search" />查询
        </Button>
      </Col>
    </Row>
    <br />
    <Row>
      <Button type="success" @click="showPeopleModal()">
        <Icon type="md-add" />添加
      </Button>
      <Button type="error" class="margin-left" @click="showDelModal">
        <Icon type="md-close" />删除
      </Button>
    </Row>
    <br />
    <Table :columns="column1" :data="data1" @on-selection-change="selectionChange"></Table>
    <br />
    <Page :total="dataTotal" show-total @on-change="page" />

    <!-- 弹窗 -->
    <Modal v-model="peopleModal" title="修改人员信息" width="50%">
      <Form
        ref="peopleForm"
        :model="peopleForm"
        :rules="ruleValidate"
        :label-width="70"
        label-position="left"
      >
        <FormItem label="编号" prop="userNo">
          <Input v-model="peopleForm.userNo"></Input>
        </FormItem>
        <FormItem label="工号" prop="userJobNo">
          <Input v-model="peopleForm.userJobNo"></Input>
        </FormItem>
        <FormItem label="姓名" prop="userName">
          <Input v-model="peopleForm.userName"></Input>
        </FormItem>
        <FormItem label="类型" prop="userType">
          <Select v-model="peopleForm.userType">
            <Option :value="1">内部</Option>
          </Select>
        </FormItem>
        <FormItem label="部门" prop="departmentId">
          <Select v-model="peopleForm.departmentId">
            <Option :value="1">1</Option>
          </Select>
        </FormItem>
        <FormItem label="科室" prop="divisionId">
          <Select v-model="peopleForm.divisionId">
            <Option :value="1">1</Option>
          </Select>
        </FormItem>
        <FormItem label="用户名" prop="userLoginName">
          <Input v-model="peopleForm.userLoginName"></Input>
        </FormItem>
        <FormItem label="密码" prop="userPwd">
          <Input type="password" v-model="peopleForm.userPwd"></Input>
        </FormItem>
        <FormItem label="角色" prop="roleId">
          <Select v-model="peopleForm.roleId">
            <Option :value="1">1</Option>
          </Select>
        </FormItem>
        <FormItem label="身份证号">
          <Input v-model="peopleForm.userIDcard"></Input>
        </FormItem>
        <FormItem label="性别">
          <Select v-model="peopleForm.gender">
            <Option :value="1">男</Option>
            <Option :value="0">女</Option>
          </Select>
        </FormItem>
        <FormItem label="地址">
          <Input v-model="peopleForm.address"></Input>
        </FormItem>
        <FormItem label="岗位">
          <Select v-model="peopleForm.occupation">
            <Option :value="1">1</Option>
          </Select>
        </FormItem>
        <FormItem label="联系电话">
          <Input v-model="peopleForm.userPhone"></Input>
        </FormItem>
        <FormItem label="照片">
          <Input v-model="peopleForm.userPhoto"></Input>
        </FormItem>
        <FormItem label="状态">
          <Select v-model="peopleForm.userState">
            <Option :value="0">可用</Option>
            <Option :value="1">禁用</Option>
          </Select>
        </FormItem>
        <br />
        <FormItem label="预览">
          <Input v-model="peopleForm.photoUrl"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="peopleModal = false">关闭</Button>
        <Button type="success" @click="savePeople">保存</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "PeopleMgr",
  data() {
    return {
      peopleModal: false,
      selectionIds: "",
      dataTotal: 0,
      isAdd: true,
      queryForm: {
        userName: "",
        userJobNo: "",
        pageIndex: 1,
        pageSize: 10
      },
      peopleForm: {
        userNo: "",
        userJobNo: "",
        userName: "",
        userType: "",
        departmentId: 0,
        divisionId: 0,
        userLoginName: "",
        roleId: 0,
        userIDcard: "",
        address: "",
        occupation: "",
        userPhone: "",
        userPhoto: "",
        photoUrl: "",
        userState: "",
        orgId: 0
      },
      ruleValidate: {
        userNo: [{ required: true, message: "编号不能为空", trigger: "blur" }],
        userJobNo: [
          { required: true, message: "工号不能为空", trigger: "blur" }
        ],
        userName: [
          { required: true, message: "姓名不能为空", trigger: "blur" }
        ],
        userType: [
          {
            required: true,
            type: "number",
            message: "类型不能为空",
            trigger: "blur"
          }
        ],
        departmentId: [
          {
            required: true,
            type: "number",
            message: "部门不能为空",
            trigger: "change"
          }
        ],
        divisionId: [
          {
            required: true,
            type: "number",
            message: "科室不能为空",
            trigger: "change"
          }
        ],
        userLoginName: [
          { required: true, message: "用户名不能为空", trigger: "blur" }
        ],
        userPwd: [{ required: true, message: "密码不能为空", trigger: "blur" }],
        roleId: [
          {
            required: true,
            type: "number",
            message: "角色不能为空",
            trigger: "change"
          }
        ]
      },
      column1: [
        {
          key: "select",
          type: "selection",
          width: 60
        },
        {
          key: "userNo",
          title: "编号",
          width: 100
        },
        {
          key: "userJobNo",
          title: "工号",
          width: 100
        },
        {
          key: "userName",
          title: "姓名",
          width: 100
        },
        {
          key: "userPhone",
          title: "联系电话",
          width: 100
        },
        {
          key: "userIDcard",
          title: "身份证号",
          width: 150
        },
        {
          key: "gender",
          title: "性别",
          width: 100
        },
        {
          key: "occupation",
          title: "岗位",
          width: 100
        },
        {
          key: "divisionName",
          title: "科室",
          width: 100
        },
        {
          key: "userState",
          title: "状态",
          width: 100
        },
        {
          title: "修改",
          width: 100,
          render: (h, params) => {
            return h(
              "a",
              {
                on: {
                  click: () => {
                    this.showPeopleModal(params.row);
                  }
                }
              },
              [
                h("Icon", {
                  props: {
                    type: "md-create",
                    size: 24
                  }
                })
              ]
            );
          }
        }
      ],
      data1: []
    };
  },
  methods: {
    selectionChange(selection) {
      this.selectionIds = selection.map(item => item.peopleId).join(",");
    },
    showDelModal() {
      this.$custom.showDelModal(this, this.delPeople());
    },
    showPeopleModal(row = {}) {
      this.peopleForm = this.$custom.handleForm(this.peopleForm, row);
      this.isAdd = Object.keys(row).length === 0;
      this.peopleModal = true;
    },
    delPeople() {
      this.$custom.post(
        "api/user/delUser",
        { ids: this.selectionIds },
        this,
        data => {
          this.$Message.success(data.msg);
          this.page();
        }
      );
    },
    page(pageIndex = 1) {
      this.queryForm.pageIndex = pageIndex;
      this.$custom.post("api/user/getUserList", this.queryForm, this, data => {
        this.data1 = data.result;
      });
    },
    savePeople() {
      let url = this.isAdd ? "api/user/addUser" : "api/user/updUser";
      this.$custom.post(url, this.peopleForm, this, data => {
        this.$Message.success(data.msg);
        this.page();
      });
    }
  },
  created() {
    this.page();
  }
};
</script>
